.footer{
    position: relative;

    width: 100%;
    height: 60px;
}
.footer-line{
    position: absolute;
    top: 0;

    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, var(--color-3) 0%, var(--color-2) 100%);
}
.footer-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    margin-bottom: 0;
    
}
.footer-tel{
    font-size: 12px;
    font-weight: 500;
}
.footer-icons{
    display: flex;
    justify-content: space-between;
    width: 50px;
}