:root{
  --color-1: #DAE4EE;
  --color-2: #374EC6;
  --color-3: #002F58;
  --color-4: #0B0B21;

  --container-margin: 5%;
  --section-between-margin: 50px;
}


body {
  min-height: 100vh;

  margin: 0;
  font-family:'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  font-weight: 600;
  line-height: 20px;
  color: var(--color-4);

  background: linear-gradient(180deg, #DAE4EE 0%, #ADB8F2 100%);
  background-repeat: no-repeat;
}

h1 {
  margin: 0;

  font-family: 'Montserrat';
  background-color: var(--color-2);
  background-image: linear-gradient(90deg, var(--color-3), var(--color-2));
  background-clip: text;
  -webkit-text-fill-color: transparent;
 }

 h2 {
  margin: 0;

  font-family: 'Montserrat';
  background-color: var(--color-2);
  background-image: linear-gradient(90deg, var(--color-3), var(--color-2));
  background-clip: text;
  -webkit-text-fill-color: transparent;
 }

p{
  margin: 0;
}

ul{
  padding: 0;
  margin: 0;

  list-style-type: none;
}

a{

  background-color: var(--color-4);
  background-clip: text;
  background-image: linear-gradient(90deg, var(--color-4), var(--color-2));
  background-size: 0;
  background-repeat: no-repeat;
  -webkit-text-fill-color: transparent;

  transition: background 0.4s ease-in-out;
}

a:hover{
  background-size: 100%;
}

.container{
  padding-right: var(--container-margin);
  padding-left: var(--container-margin);

  margin-bottom: var(--section-between-margin);
}

@media (max-width: 768px) {
  a{
    background-size: 100%;
  }
}