.project-list-item{
    display: flex;
    margin-bottom: 20px;
}
.img-container{
    margin-right: 22px;
}
.project-img{
    width: 220px;
}
.progect-text{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.progect-text-description{
    margin-bottom: 15px;

    
    white-space: pre-line;
}
/* .progect-text-link{
} */

@media (max-width: 768px) {
    .project-list-item{
        flex-wrap: wrap;
    }
    .img-container{
        margin-right: 0;
        margin-bottom: 15px;
    }
    .progect-text-link{
        width: 300px;

        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }
}