.name-container{
    display: flex;
    margin-top: 20px;
}

.avatar{
    flex-shrink: 0;
    width: 140px;
    height: 140px;

    border-radius: 5px;
    background-image: url('../../img/7_kopia\ \(2\).jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;    
}

.name-text{
    margin-left: 20px;
}

.name-header{
    margin-bottom: 20px;

    font-size: 30px;
    line-height: 38px;
}

@media (max-width: 500px) {
    .avatar {
        width: 100px;
        height: 100px;
    }
    .name-header{
        font-size: 20px;
        line-height: 20px;

    }
}