.section-header-container{
    width: 100%;
    margin-bottom: 20px;

}

.section-header{
    display: inline;
    margin-left: 10px;

    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
}

.section-header-line{
    width: 100%;
    height: 1px;
    background: linear-gradient(90deg, var(--color-3) 0%, var(--color-2) 100%);

    opacity: 0.75;
}